import React, { Component } from 'react'
import firebase from 'firebase/app'
import 'firebase/database'

import './dailyTasks.css'
import GoalList from './GoalList'

export default class DailyTasks extends Component {
    constructor(props) {
        super(props);
        this.state = {
            task: ''
        }
        this.state = {
            lists: []
        }

        this.createNote = this.createNote.bind(this);
    }
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
        //console.log(this)
    }
    createNote(e) {
        if (this.state.task !== '') {
            firebase
                .database()
                .ref('tasks')
                .push({
                    task: this.state.task
                })
        }
    }
    componentDidMount() {
        this.db = firebase.database();
        this.listenForChange();
    }
    listenForChange() {
        this.db.ref('tasks').on('child_added', snapshot => {
            let list = {
                id: snapshot.key,
                task: snapshot.val().task
            }
            let lists = this.state.lists;
            lists.push(list);
            this.setState({
                lists: lists
            });
        });

        this.db.ref('tasks').on('child_removed', snapshot => {
            let lists = this.state.lists;
            lists = lists.filter(list => list.id !== snapshot.key);

            this.setState({
                lists: lists
            });
        });
    }

    render() {
        return (
            <div className="container">
                <h1 className="title">Daily Tasks</h1>
                <div className="container">
                    <div id="add-task">
                        <input type="text" id="task" name="noteform-title" value={this.state.task || ''} onChange={this.handleChange}></input>
                        <button className="taskB" onClick={this.createNote}>create</button>
                    </div>
                    <GoalList lists={this.state.lists} />
                </div>
            </div>
        )
    }
}

