import React, { Component } from 'react'
import './index.css'
import firebase from 'firebase/app'

export default class SignUp extends Component {
    constructor(props) {
        super(props);
    this.state = {
        email: '',
        password: '',
        firstName: '',
        lastName: '',
    }
}
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    };
    handleSubmit = (e) => {
        e.preventDefault();
        const email = document.querySelector("#email").value;
        const password = document.querySelector("#password").value;
    firebase 
        .auth()
        .createUserWithEmailAndPassword(email, password)
        .then(() => {
            this.props.history.push('/dailytasks');
        });
    }

    render() {
        return (
            <div className="container">
                <div className="form-group">
                    <form id="signup" onSubmit={this.handleSubmit}>
                        <h5 className="title">Sign Up</h5>
                        <div className="form-group">
                            <input type="email" id="email" placeholder="Enter email" onChange={this.handleChange} />
                        </div>
                        <div className="form-group">
                            <input type="password" id="password" placeholder="Enter password" onChange={this.handleChange} />
                        </div>
                        <div className="form-group">
                            <input type="text" id="lastName" placeholder="Enter last name" onChange={this.handleChange} />
                        </div>
                        <div className="form-group">
                            <input type="text" id="firstName" placeholder="Enter first name" onChange={this.handleChange} />
                        </div>
                        <div className="form-group">
                            <button className="btn btn-primary">Submit</button>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}
