import React, { Component } from 'react'
import './index.css'
import firebase from 'firebase/app'

export default class LogIn extends Component {
    constructor(props) {
        super(props);
    
    this.state = {
        email: '',
        password: ''
    }
}
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }
    handleSubmit = (e) => {
        e.preventDefault();
        const email = document.querySelector("#email").value;
        const password = document.querySelector("#password").value;
        firebase 
            .auth()
            .signInWithEmailAndPassword(email, password)
            .then((user) => {
                this.props.history.push('/dailytasks');
            })
    }

    render() {
        return (     
                <div className="container">
                    <div className="form-group">
                        <form id="login" onSubmit={this.handleSubmit}>
                            <h5 className="title">Log In</h5>
                            <div className="form-group">
                                <input type="email" id="email" placeholder="Enter email" onChange={this.handleChange} />
                            </div>
                            <div className="form-group">
                            <input type="password" id="password" placeholder="Enter password" onChange={this.handleChange} />
                            </div>
                            <div className="form-group">
                                <button className="btn btn-primary">Submit</button>
                            </div>
                        </form>
                    </div>
                </div>
        )
    }
}
