import React, { Component } from 'react'
import { HashRouter, Switch, Route, NavLink } from 'react-router-dom';

import './navbar.css'
import Home from '../home/Home'
import LogIn from '../auth/LogIn'
import SignUp from '../auth/SignUp'
import DailyTasks from '../month/DailyTasks'
import ProtectedRoute from '../auth/ProtectedRoute'
import { auth } from 'firebase/app';

export default class Navbar extends Component {
    signOut = (e) => {
        e.preventDefault();
        auth().signOut();
    }
    render() {
        return (
            <HashRouter>
                <div className="navigation">
                    <nav className="navbar navbar-expand-lg navbar-light bg-light">
                        <NavLink className="navbar-brand" to="/">Bullet Journal</NavLink>
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarNav">
                            <ul className="navbar-nav">
                                <div className="signedIn">

                                    {this.props.authenticated ? (
                                        <ul className="right">
                                            <li><NavLink to='/dailytasks' className="links"
                                                style={{
                                                    textDecoration: 'none',
                                                    color: 'black'
                                                }}>Daily Tasks</NavLink></li>
                                            <li><NavLink to='/' className="links" onClick={this.signOut}
                                                style={{
                                                    textDecoration: 'none',
                                                    color: 'black'
                                                }}>Log Out</NavLink></li>
                                        </ul>
                                    ) : (
                                            <ul className="right">
                                                <li><NavLink to='/signup' className="links"
                                                    style={{
                                                        textDecoration: 'none',
                                                        color: 'black'
                                                    }}>Sign Up</NavLink></li>
                                                <li><NavLink to='/login' className="links"
                                                    style={{
                                                        textDecoration: 'none',
                                                        color: 'black'
                                                    }}>Log In</NavLink></li>
                                            </ul>
                                        )}

                                </div>

                            </ul>
                        </div>
                    </nav>
                    <div className="App">
                        <div className="homepage">
                            <Switch>
                                <Route exact path="/" component={Home} />
                                <Route authenticated={this.props.authenticated} path="/login" component={LogIn} />
                                <Route path="/signup" component={SignUp} />
                                <ProtectedRoute authenticated={this.props.authenticated} path="/dailytasks" component={DailyTasks} />
                            </Switch>
                        </div>
                    </div>
            </div>
        </HashRouter>
        )
    }
}
